#top-bar {
  background-color: green;
  position: fixed;
  top: 0;
  color: white;
  width: 100%;
  font-size: 10px;
  padding: 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.app-title {
  margin-left: 10px;
}
.settings-btn {
  margin-right: 10px;
}
#timers {
  margin-top: 55px;
}
.sitefont {
  font-family: "Raleway", sans-serif;
}
.settings {
  margin-top: -10px;
  text-align: center;
  background-color: green;
  color: white;
  font-family: "Raleway", sans-serif;
  padding: 20px;
}
.timer {
  background-color: greenyellow;
  margin: 15px;
  padding: 10px;
  padding-top: 0;
  padding-bottom: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-radius: 40px;
  padding: 10px;
}
.timer-ex {
  background-color: rgb(255, 133, 133);
  margin: 15px;
  padding: 10px;
  padding-top: 0;
  padding-bottom: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-radius: 40px;
  padding: 10px;
}
.timer-del {
  color: rgb(201, 20, 50);
}
#timer-title {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.timer-time {
  text-align: center;
}
.timer-buttons {
  flex-wrap: wrap;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.reset {
  background-color: rgb(18, 193, 209);
}
.stop {
  background-color: rgb(201, 20, 50);
}
.start {
  background-color: rgb(110, 244, 66);
}
.delete {
  background-color: orange;
}
.timer-btn {
  color: white;
  margin: 5px;
  padding: 5px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 50px;
  font-size: 19px;
}
.numinput {
  text-align: center;
  border: none;
  font-family: "Raleway", sans-serif;
  background-color: white;
  color: black;
  font-size: 30px;
  width: 100px;
}
.add-timer {
  display: block;
  margin-left: auto;
  margin-right: auto;
  background-color: green;
  border-radius: 30px;
  padding: 10px;
  text-align: center;
  color: white;
  width: 60%;
}
